.section-title {
    padding: 30px 0;
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 60px;
    position: relative;
    color: #45505b;

    &::before {
        content: '';
        position: absolute;
        display: block;
        width: 120px;
        height: 1px;
        background: #ddd;
        bottom: 1px;
        left: calc(50% - 60px);
    }

    &::after {
        content: '';
        position: absolute;
        display: block;
        width: 40px;
        height: 3px;
        background: #0563bb;
        bottom: 0;
        left: calc(50% - 20px);
    }
}